.radio-group {
  position: relative;
}
.radio-group:last-child {
  margin-bottom: 0;
}
.radio-group input {
  position: absolute;
  opacity: 0;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
}
.radio-group input:checked ~ label:after {
  opacity: 1;
}
.radio-group label {
  padding-left: 35px;
}
.radio-group label:after, .radio-group label:before {
  content: "";
  position: absolute;
  border-radius: 50%;
}
.radio-group label:before {
  left: 0;
  top: 1px;
  width: 21px;
  height: 21px;
  border: 2px solid #133FE3;
}
.radio-group label:after {
  top: 5px;
  left: 4px;
  width: 13px;
  height: 13px;
  background-color: #133FE3;
  opacity: 0;
}

